@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  min-width: 260px;
  min-height: 100vh;
  background-color: #ffffff;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  overflow-x: hidden; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* *******************
*** 2. Main styles ***
******************* */
/*
_________________________________________________

--- MEDIA QUERIES for "Mobile" viewport width ---
_________________________________________________

*/
@media (min-width: 280px) {
  .main-content {
    width: 100vw;
    min-height: 100vh;
    background-color: #f5f5f5; }
  .main-content__main-heading {
    display: inline-block;
    width: 100%;
    margin: 0 auto;
    margin-top: 40px;
    padding-left: 10px;
    padding-bottom: 10px;
    font-size: 40px;
    font-weight: 400;
    line-height: 50px;
    text-align: center;
    border-bottom: 4px double #000000; }
  /* ---------------------- End of "Mobile" MEDIA QUERIES ---------------------- */ }

/*
_________________________________________________

--- MEDIA QUERIES for "Tablet" viewport width ---
_________________________________________________

*/
@media (min-width: 768px) {
  .main-content__main-heading {
    display: inline-block;
    width: auto;
    margin: 0;
    margin-top: 40px;
    margin-left: 80px;
    padding-left: 10px;
    padding-bottom: 10px;
    font-size: 40px;
    font-weight: 400;
    line-height: 50px;
    border-bottom: 4px double #000000;
    border-left: 4px double #000000; }
  /* ---------------------- End of "Tablet" MEDIA QUERIES ---------------------- */ }

/*
_________________________________________________

--- MEDIA QUERIES for "Mobile" viewport width ---
_________________________________________________

*/
@media (min-width: 280px) {
  .game {
    display: flex;
    flex-direction: column; }
  .game__tiles {
    order: 2;
    align-self: center;
    margin-top: 50px; }
  .game__difficulty {
    order: 1;
    align-self: center; }
  .game__difficulty-heading {
    padding-bottom: 5px;
    font-size: 25px;
    font-weight: 700;
    line-height: 35px;
    border-bottom: 2px dashed #000000; }
  .game__start-button {
    width: 100%;
    min-height: 45px;
    margin-top: 50px;
    font-size: 25px;
    line-height: 30px;
    color: #000000;
    background-color: #00ff00;
    border: 1px solid #000000;
    border-radius: 30px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    transition-duration: 0.05s;
    transition-timing-function: ease;
    transition-property: color, background-color; }
  .game__loss-message {
    display: none;
    width: 100%;
    min-height: 50px;
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
    text-align: center;
    border-top: 4px double #000000;
    border-bottom: 4px double #000000; }
    .game__loss-message--show {
      display: block; }
  .game__start-button:hover,
  .game__start-button:focus {
    background-color: #32cd32; }
  .game__start-button:active {
    color: #ffffff;
    background-color: #228b22; }
  /* ---------------------- End of "Mobile" MEDIA QUERIES ---------------------- */ }

/*
_________________________________________________

--- MEDIA QUERIES for "Tablet" viewport width ---
_________________________________________________

*/
@media (min-width: 768px) {
  .game {
    justify-content: center; }
  .game__tiles {
    align-self: center;
    margin-top: 50px; }
  .game__difficulty {
    margin-right: auto;
    margin-left: 80px; }
  /* ---------------------- End of "Tablet" MEDIA QUERIES ---------------------- */ }

/*
__________________________________________________

--- MEDIA QUERIES for "Desktop" viewport width ---
__________________________________________________

*/
@media (min-width: 1150px) {
  .game {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 60px; }
  .game__tiles {
    order: 1;
    box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.5); }
  .game__difficulty {
    order: 2;
    margin: 0;
    margin-left: 70px; }
  /* ---------------------- End of "Desktop" MEDIA QUERIES ---------------------- */ }

/*
_________________________________________________

--- MEDIA QUERIES for "Mobile" viewport width ---
_________________________________________________

*/
@media (min-width: 280px) {
  .tiles {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    background-color: #663399;
    border-radius: 50%;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    transition-property: width, height; }
  .tiles__group {
    display: flex;
    margin: 0;
    padding: 0; }
    .tiles__group--top {
      margin-top: auto; }
    .tiles__group--bottom {
      margin-bottom: auto; }
  .tiles__item {
    width: 148px;
    height: 148px;
    margin: 0;
    border: 1px solid #dda0dd;
    transition-duration: 0.2s;
    transition-timing-function: ease;
    transition-property: width, height, opacity;
    opacity: 0.4;
    outline: none;
    cursor: pointer; }
    .tiles__item--top-left {
      background-color: #4169E1;
      border-top-left-radius: 248px;
      border-bottom-right-radius: 20px; }
    .tiles__item--top-right {
      background-color: #FF6347;
      border-top-right-radius: 248px;
      border-bottom-left-radius: 20px; }
    .tiles__item--bottom-left {
      background-color: #FFD700;
      border-bottom-left-radius: 248px;
      border-top-right-radius: 20px; }
    .tiles__item--bottom-right {
      background-color: #32CD32;
      border-bottom-right-radius: 248px;
      border-top-left-radius: 20px; }
  .tiles__item:hover {
    opacity: 0.7; }
  .tiles__item:focus {
    box-shadow: inset 0 0 10px 4px #ffffff; }
  .tiles__item:active {
    opacity: 1; }
  /* ---------------------- End of "Mobile" MEDIA QUERIES ---------------------- */ }

/*
_________________________________________________

--- MEDIA QUERIES for "Tablet" viewport width ---
_________________________________________________

*/
@media (min-width: 768px) {
  .tiles {
    width: 400px;
    height: 400px; }
  .tiles__item {
    width: 198px;
    height: 198px; }
  /* ---------------------- End of "Tablet" MEDIA QUERIES ---------------------- */ }

/*
__________________________________________________

--- MEDIA QUERIES for "Desktop" viewport width ---
__________________________________________________

*/
@media (min-width: 1150px) {
  .tiles {
    width: 500px;
    height: 500px; }
  .tiles__item {
    width: 248px;
    height: 248px; }
  /* ---------------------- End of "Desktop" MEDIA QUERIES ---------------------- */ }

.difficulty-levels {
  margin: 0;
  padding: 0;
  padding-left: 24px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px; }

.active {
  opacity: 1; }
